<template>
  <div class="tooltip">
    <div class="tooltip__arrow"
      v-bind:style="styleBody.arrow"></div>
    <div
      class="tooltip__body"
      ref="tooltip__body"
      v-bind:style="styleBody.body">
      <btn-icon
        @clickOnBtn="clickOnBtn({
            initDateEvent: dateEvent,
            initTimeStart: timeStart,
            initTimeEnd: timeEnd,
            initRoomId: roomId,
            initRoomTitle: roomTitle,
            initShortDateEvent: shortDateEvent,
            initEventId: eventId,
            initEventTitle: eventTitle,
            initUsers: usersArr,
            initFloor: floor
          })"
        buttonClass="btn-icon--edit"
        imgSvg="#edit"
        iconClassModifier="btn-icon__icon--edit">
      </btn-icon>

      <h2 class="tooltip__headline">{{eventTitle}}</h2>
      <p class="tooltip__place-and-data-info">
        {{shortDateEvent}}, {{timeStart}}—{{timeEnd}}
        <span class="tooltip__place-text">{{roomTitle}}</span>
      </p>
      <div class="tooltip__list-members">
        <img class="tooltip__member-img" :src="users[0].avatarUrl">
        <p class="tooltip__members">
          <span class="tooltip__name-member">{{nameGeneralMember}}</span>
          <span class="tooltip__other-members">{{otherMembers}}</span>
        </p>
      </div>
    </div>
    {{styleBody}}
  </div>
</template>

<script>
import BtnIcon from '@/components/BtnIcon'

export default {
  props: {
    roomId: String,
    roomTitle: String,
    eventId: String,
    shortDateEvent: String,
    dateEvent: String,
    timeStart: String,
    timeEnd: String,
    users: Array,
    eventTitle: String,
    floor: Number,
    mouseClickX: Number,
    bottom: Number
  },
  data: function () {
    return {
      elWidth: 0
    }
  },
  components: {
    'btn-icon': BtnIcon
  },
  computed: {
    nameGeneralMember: function () {
      const name = this.users[0].login
      return name[0].toUpperCase() + name.substring(1)
    },
    otherMembers: function () {
      const membersNum = this.users.length
      if (membersNum === 2) {
        return 'и 1 участник'
      } else if (membersNum > 2) {
        return `и ${membersNum - 1} участников`
      }
    },
    usersArr: function () {
      return this.users.map((user) => {
        return user.id
      })
    },
    styleBody: function () {
      const documentWidth = document.documentElement.clientWidth
      const halfElWidth =  this.elWidth / 2
      left = this.mouseClickX
      if(documentWidth - this.mouseClickX < halfElWidth) {
        return {
          body: {
            left: 'unset',
            right: 0 + 'px',
            top: this.bottom + 'px',
            },
          arrow: {
            'margin-left': '-8px',
            top: this.bottom + 'px',
            left: this.mouseClickX + 'px',
            'margin-right': 0}
        }
      }
      if(this.mouseClickX < halfElWidth) {
        return {
          body: {
            left: halfElWidth + 'px',
            right: 'unset',
            top: this.bottom + 'px',
            },
          arrow: {
            'margin-left': '-8px',
            top: this.bottom + 'px',
            left: this.mouseClickX + 'px',
            'margin-right': 0}
        }
      }
      return {
        body: {
          left: left + 'px',
          right: 'unset',
          top: this.bottom + 'px'},
        arrow: {
          'margin-left': '-8px',
          top: this.bottom + 'px',
          left: this.mouseClickX + 'px',
          'margin-right': 0
        }
      }
    }
  },
  mounted: function () {
    this.elWidth = this.$refs['tooltip__body'].clientWidth
  },
  methods: {
    clickOnBtn: function (data) {
      this.$emit('goToForm', data)
    }
  }
}
</script>




