<template>
  <div class="peregovorka peregovorka--selected">
    <span class="peregovorka__time">{{timeStart}}—{{timeEnd}}</span>
    <div class="peregovorka__place">
      <span class="peregovorka__name">{{roomTitle}}</span>
      <span class="peregovorka__floor">{{floor}} этаж</span>
    </div>
    <btn-icon
        @clickOnBtn="() => {}"
        imgSvg="#close"
        iconClassModifier="btn-icon__icon">
    </btn-icon>
  </div>
</template>

<script>
import BtnIcon from '@/components/BtnIcon'

export default {
  props: {
    timeStart: String,
    timeEnd: String,
    roomTitle: String,
    floor: Number
  },
  components: {
    'btn-icon': BtnIcon
  }
}
</script>



